<template>
  <div class="demo-page svg-demo">
    <el-row>

      <el-col :span="24">
        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border">
            <circle :cx="circleConfig.cx"
                    :cy="circleConfig.cy"
                    :r="circleConfig.r"
                    :stroke="circleConfig.stroke"
                    :stroke-width="circleConfig.strokeWidth"
                    :fill="circleConfig.fill" ></circle>
          </svg>
        </div>
        <div class="svg-config-form__wrapper">
          <el-form ref="circleConfigForm" :model="circleConfig" label-width="200px">
            <el-form-item label="圆心坐标(x)">
              <el-slider
                  v-model="circleConfig.cx"
                  :min="50"
                  :max="300"
                  :step="50"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="圆心坐标(y)">
              <el-slider
                  v-model="circleConfig.cy"
                  :min="50"
                  :max="300"
                  :step="50"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="半径(r)">
              <el-slider
                  v-model="circleConfig.r"
                  :min="20"
                  :max="100"
                  :step="10"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="笔划颜色(stroke)">
              <el-color-picker v-model="circleConfig.stroke" show-alpha >
              </el-color-picker>
              {{circleConfig.stroke}}
            </el-form-item>

            <el-form-item label="笔划宽度(strokeWidth)">
              <el-slider
                  v-model="circleConfig.strokeWidth"
                  :max="20"
                  :step="1"
                  show-stops>
              </el-slider>
            </el-form-item>

            <el-form-item label="填充颜色(fill)">
              <el-color-picker v-model="circleConfig.fill" show-alpha >
              </el-color-picker>
              {{circleConfig.fill}}
            </el-form-item>

          </el-form>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
export default {
  name: 'SVGDemo001',
  data () {
    return {
      circleConfig: {
        cx: 100,
        cy: 50,
        r: 40,
        stroke: 'rgba(0, 0, 0, 0.8)',
        strokeWidth: 2,
        fill: 'rgba(255, 69, 0, 0.68)'
      }
    }
  }
}
</script>

